@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.4.1/font/bootstrap-icons.css);
html,
body {
  height: 100%;
  font-family: "Montserrat", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #979797;
  font-size: 35px;
  font-weight: 600;
}

p {
  font-size: 18px;
  color: #979797;
  line-height: 1.8;
}

img {
  max-width: 100%;
}

input:focus,
select:focus,
button:focus,
textarea:focus {
  outline: none;
}

a,
a:active,
a:focus {
  text-decoration: none !important;
  outline: none;
}

ul,
ol {
  padding: 0;
  margin: 0;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

.text-center {
  text-align: center;
}

.set-bg {
  background-repeat: no-repeat;
  background-size: cover;
}

.mb-30 {
  margin-bottom: 30px;
}

button.btn-back,
button.btn-switch-lang {
  cursor: pointer;
}

textarea {
  resize: none;
}

.alert {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
}

.alert.alert-success {
  color: #155724;
  background-color: #d4edda;
  border-color: #c3e6cb;
}

.alert.alert-success .alert-link {
  color: #0b2e13;
}

.alert.alert-danger {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
}

.alert.alert-danger .alert-link {
  color: #491217;
}

/* Header Section */
.header-section {
  padding-top: 0px;
  position: relative;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.3);
}

.header-section.sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
  z-index: 2;
}

.header-section .site-logo {
  width: 130px;
  height: 100%;
  margin: 5px 0 0 0;
  text-align: center;
}

.header-section .site-logo img {
  width: 200px;
  height: 92px;
}

.main-menu ul {
  list-style: none;
  height: 100%;
  display: flex;
  margin: 0;
}

.main-menu ul li {
  display: inline-flex;
  align-items: center;
}

.main-menu ul li a {
  display: inline-flex;
  align-items: center;
  font-size: 18px;
  line-height: 1.5;
  color: #001418;
  padding: 0 15px;
  border-bottom: 1px solid transparent;
}

.main-menu ul li a span {
  display: block;
}

.main-menu ul li a.active,
.main-menu ul li a:hover {
  border-bottom: 1px solid #001418;
}

.nav-switch {
  display: none;
}

.container {
  margin-top: 100px;
}

.container.homeLarge {
  position: relative;
  background-color: black;
  height: 100vh;
  min-height: 25rem;
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  margin: 0;
}

.container.homeLarge .videoPlayer {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: 0;
  transform: translateX(-50%) translateY(-50%);
}

.container.homeLarge h4 {
  text-align: center;
}

/* Global */
h1 {
  font-size: 30px;
}

h1.section-title {
  color: #979797;
  margin: 0 0 20px 0;
}

h1.section-title span {
  display: block;
  text-align: center;
  color: #979797;
}

.container.home {
  max-width: 100%;
  margin: 0;
  padding: 100px 0 0 0;
}

.container.home .media .videoPlayer {
  width: 100%;
}

.container.home .description {
  margin-top: 40px;
  text-align: justify;
}

.container .element a.item {
  display: block;
  width: 100%;
  height: 300px;
  margin-bottom: 30px;
  position: relative;
  background-position: center center;
  transition: all 0.4s ease 0.4s;
}

.container .element a.item .pi-inner {
  position: absolute;
  width: 100%;
  height: 25%;
  left: 0;
  bottom: 0;
  background: transparent;
}

.container .element a.item .pi-inner h2 {
  opacity: 0;
  font-size: 15px;
  font-weight: 400;
  position: absolute;
  bottom: 20px;
  left: calc(50% - 50px);
  color: #fff;
  letter-spacing: 5px;
  transition: all 0.3s ease 0s;
}

.container .element a:hover .pi-inner {
  background: rgba(0, 20, 24, 0.8);
}

.container .element a:hover .pi-inner h2 {
  opacity: 1;
  letter-spacing: 2px;
}

.container .meta {
  padding-bottom: 35px;
}

.container .meta h2 {
  margin: 0;
  font-size: 20px;
  text-align: center;
}

.container .meta p {
  font-size: 14px;
  color: #a6a6a6;
  margin-bottom: 0;
}

.container button {
  color: #979797;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  background: none;
  border: none;
}

.container button:focus {
  outline: none;
}

.container button.btn-switch-lang {
  font-size: 14px;
  font-weight: normal;
}

/* ABOUT US */
.about-us {
  font-family: "Montserrat", sans-serif;
}

.about-us .top {
  margin: 0 auto 50px auto;
  text-align: center;
}

.about-us .top h1 {
  color: #979797;
  text-align: center;
  margin-top: 0;
}

.about-us .top h1 span {
  font-size: 40px;
  font-style: normal;
}

.about-us .top span {
  font-size: 14px;
  font-style: italic;
}

.about-us .description {
  text-align: justify;
}

.about-us .description img {
  padding-bottom: 15px;
}

.about-us .process h2 {
  text-align: center;
  font-size: 30px;
  margin: 50px 0 25px 0;
}

.about-us .process img {
  padding: 15px;
  object-fit: cover;
}

/* CATALOG */
.catalog .description {
  margin-bottom: 30px;
}

.catalog .area {
  margin-bottom: 30px;
}

/* Media */
.card-description a span {
  color: #333;
  font-size: 14px;
}

/* Footer */
footer {
  padding: 60px 0;
}

footer.footer-section .container-fluid .container {
  margin-top: 0;
}

footer .copyright {
  font-size: 12px;
  color: #979797;
}

footer .social-links {
  margin-bottom: 30px;
}

footer .social-links a {
  color: #979797;
  font-size: 15px;
  margin-right: 30px;
}

footer .social-links a:last-child {
  margin-right: 0;
}

footer ul {
  list-style: none;
}

footer ul.languages li {
  display: inline-block;
}

footer ul.languages li:after {
  margin: 0 10px;
  content: " • ";
}

footer ul.languages li:last-child {
  padding-right: 0;
}

footer ul.languages li:last-child::after {
  content: "";
}

footer ul.languages li a {
  color: #979797;
  font-size: 15px;
  margin-right: 30px;
  cursor: pointer;
}

footer ul.languages li a:last-child {
  margin-right: 0;
}

/* Contact */
.contact h1 {
  text-align: center;
  margin-top: 0;
}

.contact .contact-form {
  margin: 50px auto;
  padding: 20px;
}

.contact .contact-form input[type="text"],
.contact .contact-form textarea {
  box-sizing: border-box;
  width: 100%;
  display: block;
  outline: none;
  border: none;
  height: 25px;
  line-height: 25px;
  font-size: 16px;
  padding: 0;
  padding-left: 20px;
}

.contact .contact-form textarea {
  height: 200px;
}

.contact .contact-form .outline {
  display: block;
  padding: 9px;
  border: 1px solid #dddddd;
  margin-bottom: 30px;
  border-radius: 3px;
}

.contact .contact-form .outline label {
  display: block;
  float: left;
  height: 23px;
  margin-top: -25px;
  background: #fff;
  padding: 2px 5px;
  color: #b9b9b9;
  font-size: 14px;
  overflow: hidden;
}

.contact .contact-form .outline label.danger {
  color: #721c24;
  font-size: 14px;
  margin-top: 25px;
  padding: 0;
}

.contact .contact-form .btn-submit {
  display: inline-block;
  font-size: 14px;
  min-width: 140px;
  text-align: center;
  padding: 15px 10px;
  position: relative;
  color: #fff;
  border: none;
  background-color: #000;
  margin-right: 15px;
}

.contact .contact-form .btn-submit:active {
  background: #979797;
  color: #000;
}

/* Not Found */
.not-found {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.not-found .text {
  font-size: 24px;
  color: #979797;
}

/* Media Queries */
@media screen and (max-width: 991px) {
  .main-menu ul {
    display: none;
    flex-direction: column;
    background-color: #fff;
    position: absolute;
    width: 100%;
    top: 100%;
    left: 0;
    padding: 20px;
  }

  .main-menu ul.show {
    display: flex;
  }

  .main-menu ul li {
    margin-bottom: 10px;
  }

  .nav-switch {
    display: block;
    font-size: 28px;
    color: #001418;
    cursor: pointer;
  }

  .container.homeLarge {
    height: auto;
  }

  .container.homeLarge .videoPlayer {
    position: static;
    transform: none;
    width: 100%;
    height: auto;
    min-height: 300px;
  }

  .container .element a.item {
    height: 200px;
  }

  h1.section-title span {
    font-size: 24px;
  }
}

@media screen and (max-width: 768px) {
  .header-section .site-logo {
    width: auto;
    height: auto;
    margin: 10px 0;
  }

  .header-section .site-logo img {
    width: 150px;
    height: auto;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-size: 28px;
  }

  p {
    font-size: 16px;
  }

  .about-us .top h1 span {
    font-size: 30px;
  }

  .about-us .process h2 {
    font-size: 24px;
  }

  .contact .contact-form {
    padding: 10px;
  }

  .contact .contact-form .btn-submit {
    width: 100%;
    margin-bottom: 15px;
  }

  .footer .social-links a {
    margin-right: 15px;
  }
}

@media screen and (max-width: 576px) {
  .container.homeLarge .videoPlayer {
    min-height: 200px;
  }

  .about-us .top h1 span {
    font-size: 24px;
  }

  .container .element a.item {
    height: 150px;
  }

  footer ul.languages li:after {
    margin: 0 5px;
  }

  footer ul.languages li a {
    font-size: 13px;
  }
}

